import React from 'react';
import { Select } from 'antd';

const Left = (props) => {
  const { stock1, stock2, setStock1, setStock2 } = props;
  return (
    <div className="left">
      <div style={{ padding: '15px 10px' }}>
        <Select
          allowClear
          placeholder=""
          value={stock1}
          style={{ width: '100%', marginBottom: '10px' }}
          onChange={(value) => {
            setStock1(value);
          }}
          options={[
            { value: 'sh', label: '' },
            { value: 'sz', label: '' },
          ]}
        />
        <Select
          allowClear
          placeholder=""
          value={stock2}
          style={{ width: '100%' }}
          onChange={(value) => {
            setStock2(value);
          }}
          options={[
            { value: 'sh', label: '' },
            { value: 'sz', label: '' },
          ]}
        />
      </div>
    </div>
  );
};

export default Left;
